import React, { Component } from "react";
import { Link } from "react-router-dom";
import { BiArrowBack } from "react-icons/bi";
import { BsFillFileEarmarkTextFill } from "react-icons/bs";
import axios from "axios";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import { setAxiosToken } from "../../utils/AxiosToken";
import { connect } from "react-redux";
import moment from "moment-timezone";
import { TimeZone } from "../../utils/app.config";
import { Redirect } from "react-router-dom";
import {
  Auth,
  CandidateApplicationsStoreInterface,
  candidateExamStoreInterface,
  FC_Access,
  FC_GetProfileDetails,
  LoadCandidateApplications,
  setSelectedCandidateExam,
  setSelectedCourse,
  setSelectedPosition,
  InitializeExamDetails,
  loadExamsDone,
  AddUserApplication,
  loadApplicationPeriods,
} from "../../actions";
import { StoreState } from "../../reducers";
import { UserExamStatus, UserExamsListInterface } from "../../interfaces/exam";
import Loading from "../../components/Loading/Loading";
import { DATE, getMinutesFromTwoDates } from "../../utils/functions";
import { CountDown } from "../../components/CountDown/CountDown";
import { ExamTimeStatus, validateExamDate } from "../../utils/question_bank";
import { course, PositionAPI } from "../../interfaces";
import {
  ApplicationStates,
  GetApplicationsAPI,
} from "../../interfaces/application";
import { UserAccessList } from "../../utils/auth_access";

interface NotPreparedExam {
  course: string;
  course_name: string;
  course_code: string;
  position: string;
}
interface CandidateExamsProps {
  FC_GetProfileDetails: Function;
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  candidateApplications: CandidateApplicationsStoreInterface;
  LoadCandidateApplications: (
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
  setSelectedCandidateExam: (exam_id: string) => void;
  setSelectedCourse: (course: course | null) => void;
  setSelectedPosition: (position: PositionAPI) => void;
  InitializeExamDetails: (
    periodId: string,
    courseId: string,
    district_id: string,
    center_id: string,
    room_id: string,
    position_id: string,
    callback: (loading: boolean) => void
  ) => void;
  loadExamsDone: (
    periodId: string,
    userId: string,
    callback: (loading: boolean) => void
  ) => void;
  AddUserApplication: (application: GetApplicationsAPI) => void;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
}
interface candidatePendingExamsList {
  preparedExam: UserExamsListInterface[];
  notPreparedExam: NotPreparedExam[];
}
interface CandidateExamsState {
  loading: boolean;
  candidateExams: candidatePendingExamsList | null;
  activeExams: string[];
  candidateApplications: GetApplicationsAPI[] | null;
}

export class _CandidateExams extends Component<
  CandidateExamsProps,
  CandidateExamsState
> {
  constructor(props: CandidateExamsProps) {
    super(props);

    this.state = {
      loading: false,
      candidateExams: null,
      activeExams: [],
      candidateApplications: null,
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  loadCandidateExams = async (user: string, period: string) => {
    this.setLoading(true);
    setAxiosToken();
    try {
      const res = await axios.get<candidatePendingExamsList>(
        `${API.QBank}/exam/user/${user}/${period}`
      );
      this.setState({ candidateExams: res.data });
      // console.log("My exams list: ", res.data);
    } catch (error: any) {
      console.log("My exams error: ", { ...error });
      alert(errorToText(error));
    }
    this.setLoading(false);
  };
  loadUserProfileDetails = () => {
    this.setState({
      loading: true,
    });
    this.props.FC_GetProfileDetails(
      this.props.auth.user.user_id,
      (status: boolean, msg: string) => {
        if (status === true) {
          this.setState({
            loading: false,
          });
        } else {
          this.setState({
            loading: false,
          });
        }
      }
    );
    this.setState({
      loading: false,
    });
  };

  InitializationFn = (application: GetApplicationsAPI) => {
    if (this.props.candidateExam.user_exams_done.length === 0) {
      this.props.loadExamsDone(
        application.application_session.session_plan_id,
        this.props.auth.user.user_id,
        this.setLoading
      );
    }
    for (let course of application.position.courses) {
      let testDone = this.props.candidateExam.user_exams_temp.find(
        (itm) => itm.course === course.course_id
      );
      if (testDone === undefined) {
        let center_id = application.exams.find(
          (itm) => itm.course_id === course.course_id
        );
        if (center_id !== undefined) {
          this.props.InitializeExamDetails(
            application.application_session.session_plan_id,
            course.course_id,
            center_id.district_id,
            center_id.center_id,
            center_id.room_id,
            application.position.position_id,
            this.setLoading
          );
        }
      }
    }
  };
  loadUserApplications = async () => {
    this.setLoading(true);
    try {
      const res = await axios.get<GetApplicationsAPI[]>(
        `${API.APPLICATION}/application/${this.props.auth.user.user_id}`
      );
      if (res.status === 200) {
        this.setState({ candidateApplications: res.data });
        for (let application of res.data) {
          this.InitializationFn(application);
        }
        if (
          this.props.candidateApplications.candidate_applications === null ||
          this.props.candidateApplications.candidate_applications.length === 0
        ) {
          for (let application of res.data) {
            this.props.AddUserApplication(application);
          }
        }
      }
    } catch (error: any) {
      console.log("Appl ex Err: ", { ...error });
    }
    this.setLoading(false);
  };
  findLocation = (position_id: string, course_id: string) => {
    if (this.state.candidateApplications !== null) {
      const test = this.state.candidateApplications.find(
        (item) =>
          item.position.position_id === position_id &&
          item.exams.filter((itm) => itm.course_id === course_id)
      );
      if (test !== undefined) {
        let course = test.exams.find((itm) => itm.course_id === course_id);
        if (course !== undefined) {
          return course;
        }
      }
    }
    return null;
  };
  componentDidMount = () => {
    this.props.loadApplicationPeriods(this.setLoading);

    if (
      this.props.auth.user.sex === "" ||
      this.props.auth.user.sex.length === 0
    ) {
      this.loadUserProfileDetails();
    }
    this.loadUserApplications();
    if (this.props.auth.application_session !== null) {
      this.loadCandidateExams(
        this.props.auth.user.user_id,
        this.props.auth.application_session.session_plan_id
      );
    }
    if (this.props.auth.application_session !== null) {
      this.props.loadExamsDone(
        this.props.auth.application_session.session_plan_id,
        this.props.auth.user.user_id,
        this.setLoading
      );
    }
  };

  removeDuplicatesExams = (
    exams: UserExamsListInterface[]
  ): UserExamsListInterface[] => {
    let temp: UserExamsListInterface[] = [];
    for (let item of exams) {
      if (temp.find((itm) => itm._id === item._id) === undefined) {
        temp = [...temp, item];
      }
    }
    return temp;
  };

  removeDuplicatesNotPreparedExams = (
    exams: NotPreparedExam[]
  ): NotPreparedExam[] => {
    let temp: NotPreparedExam[] = [];
    for (let item of exams) {
      if (temp.find((itm) => itm.course === item.course) === undefined) {
        temp = [...temp, item];
      }
    }
    return temp;
  };
  setTimeOut = (exam_id: string) => {
    let temps: string[] = this.state.activeExams;
    this.setState({
      activeExams: [...temps, exam_id],
    });
  };
  selectApplication = (
    applications: GetApplicationsAPI[] | null,
    position_id: string,
    course_id: string
  ): GetApplicationsAPI | null => {
    if (applications !== null) {
      let temp = applications.find(
        (item) => item.position.position_id === position_id
      );
      if (temp !== undefined) {
        if (
          temp.position.courses.find((itm) => itm.course_id === course_id) !==
          undefined
        ) {
          return temp;
        }
      }
    }
    return null;
  };
  render() {
    const today = moment().tz(TimeZone);
    // console.log("TODAY: ", today.get("days"));
    if (!FC_Access(this.props.auth, UserAccessList.MY_APPLICATION).view) {
      return <Redirect to="/" />;
    }
    if (this.props.candidateExam.selected_exam_id !== null) {
      return <Redirect to="/do-exam" />;
    }
    return (
      <div className="mt-2 -ml-2 mr-2">
        <div className="rounded mt-2">
          <div className="flex flex-row items-center gap-3">
            <Link
              to="/apps-page"
              title="Back to menus"
              className="bg-white group hover:bg-blue-50 h-10 w-10 flex items-center justify-center rounded-full cursor-pointer"
            >
              <BiArrowBack className="text-3xl text-blue-600 group-hover:text-blue-800" />
            </Link>
            <div className="flex flex-col">
              <span className="font-extrabold text-3xl">My exams</span>
              <span className="text-gray-600 text-sm">
                List of exams that are assigned to me
              </span>
            </div>
          </div>
        </div>
        {this.state.loading === true ||
        this.state.candidateExams === null ||
        this.state.candidateApplications == null ? (
          <div className="px-4 pt-5 pb-1 bg-white mt-2 rounded">
            <Loading />
          </div>
        ) : this.removeDuplicatesExams(this.state.candidateExams.preparedExam)
            .length === 0 ? (
          <div className="bg-gray-50 rounded px-4 py-5 text-center font-bold mt-2 text-2xl text-gray-400">
            No exams found
          </div>
        ) : (
          this.removeDuplicatesExams(
            this.state.candidateExams.preparedExam
          ).map((item, i) => (
            <div key={i + 1} className="my-2 rounded bg-white p-2 group">
              <div className="flex flex-row items-center justify-between">
                <div className="flex flex-row items-center gap-2 w-full">
                  <div>
                    <BsFillFileEarmarkTextFill className="text-4xl text-gray-600 group-hover:text-blue-600" />
                  </div>
                  <div className="flex flex-col w-full">
                    <span className="text-base text-gray-800 font-extrabold group-hover:text-blue-600">
                      {item.course_name}
                    </span>
                    <div className="flex flex-row items-center justify-start w-full gap-3 -mt-1">
                      <div className="text-sm text-gray-500 group-hover:text-blue-600">
                        Date: {DATE(item.exam_date, "DD/MM/YYYY")}
                      </div>
                      <div className="hidden md:block text-sm bg-gray-100 underline text-gray-500  font-bold rounded-full px-2">
                        Start time: {item.start_time}
                      </div>
                      <div className="hidden md:block text-sm bg-blue-100 text-blue-900  font-bold rounded-full px-2">
                        Status: {item.user_status}
                      </div>
                    </div>
                    {this.findLocation(item.position, item.course) !== null && (
                      <div className="flex flex-row items-center gap-2 mt-2">
                        <div>
                          Exam center:{" "}
                          <span className="text-sm bg-gray-100 text-blue-900 group-hover:text-blue-600 font-bold rounded-full px-2 w-max">
                            {
                              this.findLocation(item.position, item.course)
                                ?.center_name
                            }
                          </span>
                        </div>
                        <div className="w-max">
                          Room number:{" "}
                          {this.findLocation(item.position, item.course)
                            ?.room_number === 0 ? (
                            <span className="text-sm bg-yellow-100 text-yellow-900 group-hover:text-yellow-800 font-bold rounded-full px-2">
                              Waiting room
                            </span>
                          ) : (
                            <span className="text-sm bg-gray-100 text-blue-900 group-hover:text-blue-600 font-bold rounded-full px-2">
                              {
                                this.findLocation(item.position, item.course)
                                  ?.room_number
                              }
                            </span>
                          )}
                        </div>
                      </div>
                    )}
                  </div>
                </div>
                <div className="lg:w-full flex flex-row items-center justify-end">
                  {this.state.activeExams.find((itm) => itm === item._id) !==
                    undefined ||
                  item.user_status === UserExamStatus.Editing ||
                  item.user_status === UserExamStatus.Paused ? (
                    validateExamDate(
                      item.exam_date,
                      item.start_time,
                      item.end_time
                    ) !== ExamTimeStatus.Expired ? (
                      this.selectApplication(
                        this.state.candidateApplications,
                        item.position,
                        item.course
                      )?.application_status !==
                      ApplicationStates.Shortlisted ? (
                        <div className="text-yellow-800 font-extrabold text-base bg-yellow-100 rounded px-3 py-1 cursor-not-allowed">
                          Not shortlisted
                        </div>
                      ) : (
                        <div
                          onClick={() => {
                            let application = this.selectApplication(
                              this.state.candidateApplications,
                              item.position,
                              item.course
                            );
                            if (application !== null) {
                              if (
                                application.application_status ===
                                ApplicationStates.Shortlisted
                              ) {
                                // Select exam
                                this.props.setSelectedPosition(
                                  application.position
                                );
                                this.props.setSelectedCourse(
                                  application.position.courses.find(
                                    (itm) => itm.course_id === item.course
                                  )!
                                );
                                this.props.setSelectedCandidateExam(item._id);
                              } else {
                                alert(
                                  "Corresponding application is not shortlisted!"
                                );
                              }
                            } else {
                              alert("Invalid exam!");
                            }
                          }}
                          className={`${
                            item.user_status === UserExamStatus.Editing ||
                            item.user_status === UserExamStatus.Paused
                              ? "bg-green-600 group-hover:bg-green-800"
                              : "bg-blue-600 group-hover:bg-blue-800"
                          } btn  text-white cursor-pointer w-max`}
                        >
                          {item.user_status === UserExamStatus.Editing
                            ? "Back to Exam"
                            : item.user_status === UserExamStatus.Paused
                            ? "Paused, back to exam"
                            : "Start Exam"}
                        </div>
                      )
                    ) : item.user_status === UserExamStatus.Editing ||
                      item.user_status === UserExamStatus.Done ||
                      item.user_status === UserExamStatus.Paused ||
                      item.user_status === UserExamStatus.Removed ? (
                      <div className="text-blue-700 flex flex-col items-end justify-center w-max">
                        <span>Marks</span>
                        <span className="text-2xl font-extrabold -mt-2">
                          {Math.round(item.user_marks)}%
                        </span>
                      </div>
                    ) : (
                      <div className="bg-yellow-100 text-yellow-700 font-extrabold text-base px-3 py-1 rounded-md w-max">
                        Expired
                      </div>
                    )
                  ) : item.found === false ? (
                    <div
                      title="Please wait for the exam time"
                      className="text-lg lg:text-xl font-extrabold bg-green-100 text-green-700 px-3 py-1 rounded cursor-not-allowed w-max"
                    >
                      <CountDown
                        start_time={new Date(today.toString()).toString()}
                        duration_min={getMinutesFromTwoDates(
                          new Date(today.toString()),
                          new Date(
                            DATE(item.exam_date, "MM/DD/YYYY") +
                              " " +
                              item.start_time
                          )
                        )}
                        timeout={() => this.setTimeOut(item._id)}
                        returnString={true}
                        labels={true}
                        days
                      />
                    </div>
                  ) : (
                    <div className="text-blue-700 flex flex-col items-end justify-center w-max">
                      <span>Marks</span>
                      <span className="text-2xl font-extrabold -mt-2">
                        {Math.round(item.user_marks)}%
                      </span>
                    </div>
                  )}
                </div>
              </div>
            </div>
          ))
        )}
        {/* Not prepared exams */}
        {this.state.candidateExams !== null &&
          this.removeDuplicatesNotPreparedExams(
            this.state.candidateExams.notPreparedExam
          ).length > 0 &&
          this.removeDuplicatesNotPreparedExams(
            this.state.candidateExams.notPreparedExam
          ).map((item, i) => (
            <div key={i + 1} className="my-2 rounded bg-white p-2 py-3 group">
              <div className="flex flex-row items-center justify-between">
                <div className="flex flex-row items-center gap-2">
                  <div>
                    <BsFillFileEarmarkTextFill className="text-4xl text-gray-300 group-hover:text-blue-600" />
                  </div>
                  <div className="flex flex-col">
                    <span className="text-base text-gray-500 font-bold group-hover:text-blue-600">
                      {item.course_name}
                    </span>
                    <div className="flex flex-row items-center gap-3 -mt-1">
                      <div className="text-sm text-gray-500 group-hover:text-blue-600">
                        Not prepared
                      </div>
                    </div>
                  </div>
                </div>
                <div className="bg-gray-100 text-gray-600 font-extrabold text-base px-3 py-1 rounded-md">
                  Not prepared
                </div>
              </div>
            </div>
          ))}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  candidateExam,
  candidateApplications,
}: StoreState): {
  auth: Auth;
  candidateExam: candidateExamStoreInterface;
  candidateApplications: CandidateApplicationsStoreInterface;
} => {
  return {
    auth: auth,
    candidateExam: candidateExam,
    candidateApplications: candidateApplications,
  };
};

export const CandidateExams = connect(mapStateToProps, {
  FC_GetProfileDetails,
  LoadCandidateApplications,
  setSelectedCandidateExam,
  setSelectedCourse,
  setSelectedPosition,
  InitializeExamDetails,
  loadExamsDone,
  AddUserApplication,
  loadApplicationPeriods,
})(_CandidateExams);
